@font-face {
    font-family: 'Space Mono';
    src: url('./assets/fonts/SpaceMono-Bold.ttf');
    font-weight: 700;
    font-style: normal;
}

:root {
    --strong-cyan: hsl(172, 67%, 45%);
    --soft-mint-green:  hsl(172, 61%, 77%);
    --soft-aqua: hsl(173, 61%, 77%);
    --very-dark-cyan: hsl(183, 100%, 15%);
    --dark-cyan: hsl(183, 78%, 24%);
    --dark-grayish-cyan: hsl(186, 14%, 43%);
    --grayish-cyan: hsl(184, 14%, 56%);
    --desaturated-cyan: hsl(183, 15%, 56%);
    --light-grayish-cyan: hsl(185, 41%, 84%);
    --very-light-graying-cyan: hsl(189, 41%, 97%);
    --white: hsl(0, 0%, 100%);
    --soft-coral-pink: hsl(13, 70%, 60%);
    --coral-pink: hsl(13, 70%, 61%);

    font-family: 'Space Mono', sans-serif;
    font-weight: 700;
}

body {
    margin: 0;
}